html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  position: relative;
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-width: 360px;
  min-height: 100vh;
  margin: 0;
  font-family: "Raleway", "Arial", sans-serif;
  font-weight: 500;
  font-size: 15px;
  line-height: 1.5;
  color: #e5e5e5;
  background-color: #1c1c1c;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.container {
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 32px;
}

@media (max-width: 1023px) {
  .container {
    max-width: 1023px;
  }
}

@media (max-width: 767px) {
  .container {
    max-width: 767px;
    padding: 0 16px;
  }
}

.body-lock {
  position: fixed;
  width: 100%;
  overflow-y: scroll;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

.overlay--hidden {
  display: none;
}
